function Home() {
    return (
        <div className="home">
            <div className="home__container">
                <img
                className="home__image"
                 src="https://occ-0-3924-3467.1.nflxso.net/dnm/api/v6/LmEnxtiAuzezXBjYXPuDgfZ4zZQ/AAAABSUUW3ROmxS7i85S0HroyoJge_Dj2J5JID66HCmAqnzWSNdWcdAsi4Hb5NA0aaF3hkyk4BCmosWYQKICG9dfWX8dBPEDBSRB-_JN.png?r=81f"
                 alt=""
                 />
                 {data.map((card, index) => (
                  <div key={index} className="home__row">
                  <Product
                    title={card.title}
                    title2={card.title2}
                    price={card.price}
                    image={card.imageUrl}
                  />
                  </div>
                 ))}
 </div>
</div>
    );
}

export default Home;