import React from "react";
import "./Product.css";

function Product({ title, image, price, title2  }) {
 <h1>helloooo</h1>
  return (
    <div className="product">
      <div className="product__info">
        <p>{title}</p>
        <div className="product__info2">
        <p>{title2}</p></div>
        <p className="product__price">
          <small>лв.</small>
          <strong>{price}</strong>
        </p>

      </div>

      <img src={image} alt="" />
      

    </div>
  );
}

export default Product;